<template>
<el-main>
<!-- 信息 -->
<div class="layout-content">
<el-descriptions
  direction="vertical"
  :column="2"
  :size="size"
  border
>
  <el-descriptions-item label="职工姓名">{{tname}}</el-descriptions-item>
  <el-descriptions-item label="职工号">{{tno}}</el-descriptions-item>
  <el-descriptions-item label="职工姓别">{{tsex}}</el-descriptions-item>
  <el-descriptions-item label="职工年龄">{{tage}}</el-descriptions-item>
  <el-descriptions-item label="职工职称">{{ttitle}}</el-descriptions-item>
  <el-descriptions-item label="联系电话">{{tphone}}</el-descriptions-item>
</el-descriptions>
</div>
</el-main>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  computed: {
    ...mapGetters(['getUsername']),
    username() {
      return this.getUsername;  // 直接访问 getter 属性
    }
  },
  data(){
    return{
      tname:'',
      tno:'',
      tsex:'',
      tage:'',
      ttitle:'',
      tphone:''
    }
  },
  mounted(){
    let params = new URLSearchParams();     
    params.append('username', this.username);  
    console.log(this.username)
      
    this.$axios({
      method: "post",    
      url: "http://127.0.0.1:8689/showtehinfo",    
      data: params,    
      headers: {    
        'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'    
      }    
    }).then((result) => {  
        console.log(result.data);
        this.tname = result.data[0]
        this.tno = result.data[1]     
        this.tsex = result.data[2]
        this.tage = result.data[3]
        this.ttitle = result.data[4]
        this.tphone = result.data[5]
      }).catch((error) => {  
        console.error('请求失败:', error);  
      });
  }


  
}
</script>

